/* Social Icons in top bar*/
#topbar-icons {
  width: 100%;
  position: absolute; }
  #topbar-icons #social-icons-all {
    float: right;
    z-index: 200;
    top: 0; }
    #topbar-icons #social-icons-all .social-icon-container {
      display: inline-block;
      margin-top: 5px; }
      #topbar-icons #social-icons-all .social-icon-container:last-child {
        margin-right: 15px; }
      #topbar-icons #social-icons-all .social-icon-container .social-icon-all {
        font-size: 26px; }
        #topbar-icons #social-icons-all .social-icon-container .social-icon-all:hover {
          color: #8E221D; }

/* Social Icons topbar  Responsive */
@media (max-width: 600px) {
  #topbar-icons #social-icons-all .social-icon-all {
    font-size: 22px; } }

